import { ChatWatchlistEntity } from "@rp/common/api-types"
import { Sector, WorkflowDescriptionResult } from "@rp/react/tanstack-api/workflow"

type WorkflowData = {
  selectedCompanies?: Array<ChatWatchlistEntity>
  selectedTimeframe?: TimeframeOption
  selectedSector?: Sector
  customInstructions?: string
}

type WorkflowState = {
  originWorkflow: WorkflowDescriptionResult
  selectedData?: WorkflowData
}

type TimeframeOption = {
  label: string
  start: Date | string
  end: Date | string
}

enum WorkflowSteps {
  SELECT_COMPANIES = 0,
  SELECT_TIMEFRAME = 1,
  SELECT_SOURCES = 2,
}

export { WorkflowSteps }
export type { TimeframeOption, WorkflowData, WorkflowState }
